/* eslint-disable prefer-promise-reject-errors */
import axios from 'axios' // axios
import Qs from 'qs' // qspost
import Router from '../router/index'

import {
  Message
} from 'element-ui'

import {
  checkStatus
} from './utils' // 

// axios
const instance = axios.create({
  // baseURL: process.env.BASE_URL,
  timeout: 30000, // 
  // `transformRequest` 
  transformRequest: [function (data) {
    //  data 
    return data
  }],
  // `transformResponse`  then/catch 
  transformResponse: [function (data) {
    //  data 
    return data
  }]
})
// 
instance.interceptors.request.use(function (config) {
  // ...
  let token = localStorage.getItem('token')
  // let {token} = JSON.parse(tokenid2) || {token: ''}

  config.headers = Object.assign(config.method === 'get' ? {
    'Accept': 'application/json',
    'Content-Type': 'application/json; charset=UTF-8'
  } : {
    'Content-Type': 'application/json; charset=UTF-8'
  }, {'x-token': token, ...config.headers})

  if (config.method === 'post') {
    const contentType = config.headers['Content-Type']
    // Content-Typedata
    if (contentType) {
      if (contentType.includes('multipart')) { //  'multipart/form-data;'
        // config.data = data;
      } else if (contentType.includes('json')) { //  'application/json;'
        // raw body() "{name:"nowThen",age:"18"}"
        config.data = JSON.stringify(config.data)
      } else { //  'application/x-www-form-urlencoded;'
        // raw body() name=nowThen&age=18
        config.data = Qs.stringify(config.data)
      }
    }
  }
  return Promise.resolve(config)
}, function (error) {
  // ...
  return Promise.reject(error)
})

// 
instance.interceptors.response.use(function (response) {
  // ...
  if (response.headers['x-token']) {
    localStorage.setItem('token', response.headers['x-token'])
  }

  // const { reason_code } = response.data || {};
  // if (reason_code === '001') { // 
  //   const instance = Toast('...');
  //   setTimeout(() => {
  //     instance.close();
  //     window.location.href = '/login';
  //   }, 3000)
  // }
  return Promise.resolve(checkStatus(response))
}, function (error) {
  // ...
  if (error.response) {
    return Promise.reject(checkStatus(error.response))
  } else if (
    error.code === 'ECONNABORTED' &&
    error.message.indexOf('timeout') !== -1
  ) {
    // eslint-disable-next-line prefer-promise-reject-errors
    return Promise.reject({
      msg: ''
    })
  } else {
    return Promise.reject({})
  }
})

let baseUrl = process.env.API // url

// request.js
const request = async function (opt) {
  let options
  try {
    options = Object.assign({
      method: 'get',
      ifHandleError: false, // ()
      ifHandleOk: false
    }, opt)
    //  proxy 
    options.baseURL = baseUrl

    const res = await instance(options)
    let parseData = JSON.parse(res)
    if (parseData.Code !== 0 && options.ifHandleError) { // 
      Message.error(parseData.Msg || '')
    }
    if (parseData.Code === 0 && options.ifHandleOk) { // 
      Message.success(parseData.Msg || '')
    }
    if (parseData.Code === 2 && window.location.href.indexOf('details') === -1) { // 
      Router.push('/login')
    }
    if (parseData.Code === 2 && parseData.Msg === '') { // 
      Router.push('/login')
      localStorage.removeItem('token')
      localStorage.removeItem('info')
    }
    return parseData
  } catch (err) {
    if (options.ifHandleError) { // 
      Message.error('')
    }
    return err
  }
}

export default request



