<template>
  <el-table :data="tableData" style="width: 100%">
    <el-table-column prop="coin_type" label="">
      <template slot-scope="scope">{{type[scope.row.coin_type]}}</template>
    </el-table-column>
    <el-table-column prop="buy_type" label="">
      <template slot-scope="scope">{{scope.row.buy_type===1?'':""}}</template>
    </el-table-column>
    <el-table-column prop="multiple" label=""></el-table-column>
    <el-table-column prop="count" label=""></el-table-column>
    <el-table-column prop="open" label=""></el-table-column>
    <el-table-column prop="promise" label=""  width="120"></el-table-column>
    <el-table-column prop="show_open_date" label="" width="180"></el-table-column>

    <el-table-column prop="state" label="">
      <template slot-scope="scope">{{state[scope.row.state]}}</template>
    </el-table-column>
    <el-table-column prop="State" label="">
      <template slot-scope="scope">
        <el-button type="text" @click="orderCancel(scope.row,scope.tid)"></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
import { orderCancel, orderList } from '../../../services/index'

const state = {
  0: '',
  1: '',
  2: '',
  3: ''
}
let obj = {
  0: 'btc',
  1: 'eth',
  2: 'eos',
  3: 'etc',
  4: 'ltc',
  5: 'bch',
  6: 'xrp'
}
export default {
  data () {
    return {
      state: state,
      type: obj,
      tableData: []
    }
  },
  mounted () {
    this.orderList()
  },
  methods: {

    async orderCancel ({ tid }) {
      await orderCancel({ id: tid.toString() })
      await this.orderList()
      await this.$parent.reloadassets()
    },
    async orderList () {
      let { Data } = await orderList({
        page: 1,
        size: 100,
        type: 1
      })
      console.log(Data.list)
      this.tableData = Data.list
    }
  }
}
</script>



